<template>
        <transition name="slide-fade">
          <div class="pechat">
            <h3>ÐÐ¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ð²Ð¸Ð´Ð¸Ð¼Ð¾Ð¹ Ð¿ÐµÑÐ°ÑÐ¸:</h3>

            <!--
              <img width="595" height="842" v-if="doc_prev" :src="doc_prev" id="watermarked">
            -->
              <div v-html="doc_prev"></div>

            <div class="text-center pt-20">
              <p>ÐÐ¾Ð´ÑÐ²ÐµÑÐ´Ð¸ÑÑ Ð²ÑÑÑÐ°Ð²Ð»ÐµÐ½Ð½Ð¾Ðµ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ð²Ð¸Ð´Ð¸Ð¼Ð¾Ð¹ Ð¿ÐµÑÐ°ÑÐ¸</p>
              <button class="btn-3d-2" @click="podpisat">ÐÐ¾Ð´Ð¿Ð¸ÑÐ°ÑÑ</button>
            </div>
